//这里定义不需要渲染到模板的变量
export default {
	data() {
		return {
			datas: '',
			options: '',
		}
	},
	//监听页面加载，其参数为上个页面传递的数据，参数类型为Object（用于页面传参）
	onLoad(options) {
		var that = this;
		that.options = options;
		that.getData();
	},
	//监听页面显示。页面每次出现在屏幕上都触发，包括从下级页面点返回露出当前页面
	onShow() {

	},
	//监听页面初次渲染完成。注意如果渲染速度快，会在页面进入动画完成前触发
	onReady() {},
	//监听页面隐藏
	onHide() {},
	//监听页面卸载
	onUnload() {},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {},
	methods: {
		saveformid(e) {
			this.addFormID(e.detail.formId);
		},
		//自定义方法
		getData() {
			var that = this;
			this.$http.post("orders/show", {
				oid: that.options.oid
			}).then((res) => {
				console.log("获取订单详情");
				that.datas = res.data;
			})
		},

		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
		// 确认收货
		shouhuo(e) {
			var that = this;
			uni.showModal({
				title: "温馨提示",
				content: "是否确认收货?",
				success(res) {
					if (res.confirm) {
						console.log('确认收货')
						var oid = that.options.oid;
						that.$http.post("orders/confirm_receipt", {
							oid: oid
						}).then((res) => {
							console.log(res.data);
							if (res.code == 1) {
								uni.showToast({
									title: '收货完成',
									duration: 2000,
									success(res) {
										setTimeout(function() {
											that.pages = 0;
											that.datas = [];
											that.getData();
										}, 2000)

									},
								})
							}
						})
					}
				},
			})

		},
		
		check_express(e){
			var that = this;
			var kuaidi = e.currentTarget.dataset.kuaidi;
			var kdcode = e.currentTarget.dataset.kdcode; 
			uni.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14',
				path: 'pages/result/result?nu='+kuaidi+'&com='+kdcode+'&querysource=third_xcx',
				success(res) {
					// 打开成功
				}
			})
		},
	}
}
